/*

 (IE8)

*/

/* ----  ---- */

.notes{
	margin-left:1.3em !important;
	text-indent:-1.3em !important;
	&:before{
		content:" ";
	}
	& > *{
		text-indent:0;
	}
}


/* ---- n ---- */

.notes_num{
	counter-increment:decimal_notes;
	margin-left:1.9em !important;
	text-indent:-1.9em !important;
	&:before{
		content:""counter(decimal_notes)" ";
	}
	& > *{
		text-indent:0;
	}
}
